
















































































































































.message-table {
    margin-bottom: 40px
}

.form-inline.submit {
    position: fixed;
    bottom: 3%;
    width: 70%;
}

.userchoice {
    position: fixed;
    top: 90px;
    /*background-color: red;*/
    height: 40px;
    width: 80%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    color: white;
    text-align: center;
    font-size: 20px
}

.userchoice div {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1
}

.users {
    margin-top: 50px;
    background-color: #a8bcf5;
    height: 100%;
    width: 350px;
}

.people {
    margin: 20px;
    height: 60px
}

.people .img {
    width: 50px;
    height: 50px;
    overflow: hidden;
    border-radius: 50%;
    border: 2px solid #fff;
    box-shadow: 0 3px 2px rgba(0, 0, 0, .4);
}

.people .name {
    position: relative;
    left: 80px;
    top: -50px;
    font-size: 20px;
}

.people .title {
    position: relative;
    left: 80px;
    top: -50px;
    font-size: 15px;
}

.people .online {
    position: relative;
    left: 280px;
    top: -85px;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    border: 2px solid #fff;
    box-shadow: 0 3px 2px rgba(0, 0, 0, .4);
    background-color: grey;
}

.people .online.active {
    background-color: green;
}

img {
    width: 100%
}

#page3-content {
    overflow: auto;
    height: 100%;
}

@media (max-width: 768px) {
    .form-inline .form-control {
        display: inline-block;
        width: auto;
        vertical-align: middle;
    }
}



